  body {
      font-family: Arial, sans-serif;
      background-color: #f2f2f2;
  }

  .container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
  }

  form {
      background-color: white;
      border-radius: 10px;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
      padding: 20px;
      max-width: 400px;
      margin: 0 auto;
  }

  label {
      display: block;
      margin-bottom: 10px;
      font-weight: bold;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"] {
      display: block;
      width: 90%;
      padding: 10px;
      border: 1px solid #ccc;
      border-radius: 5px;
      margin-bottom: 20px;
  }

  input[type="submit"] {
      background-color: #4CAF50;
      color: white;
      padding: 10px 20px;
      border: none;
      border-radius: 5px;
      cursor: pointer;
  }